body {
  background-color: #020304;
  color: white;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

header {
  background-color: #2c4d56;
  color: #020304;
  padding: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
}

header input {
  margin-right: 15px;
  width: 500px;
  height: 35px;
  border: 1px solid;
  outline: none;
  background-color: #626266;
  border-radius: 8px;
  font-size: 18px;
  font-weight: 500;
  border: none;
  padding: 10px;
  color: #fff;
}

input::placeholder{
  color: #fff;
}

header button{
  width: 52px;
  height: 53px;
  border: none;
  background-color: #626266;
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  cursor: pointer;
  border-radius: 8px;
}

main{
  margin: 0;
  margin-top: 45px;
}

ul {
  margin: 0;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(44ch, 1fr));;
  justify-items: center;
  padding-inline-start: 0px !important;
}

ul li {
  text-align: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  font-size: 20px;
  margin: 5px;
  text-align: center;
  width: 300px;
  height: 500px;
  background-color: #2c4d56;
  padding: 10px;
  padding-top: 25px;
  border-radius: 10px;
  margin-top: 10px;
  overflow: auto;
}

ul li img {
  margin-top: 18px;
  width: 135px;
  margin-bottom: 15px;
}


@media (max-width: 910px) {
  header{
    display: block;
    text-align: center;
  }

  .inputButton{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  button{
    margin-top: 10px;
  }

  input{
    margin: 0 auto;
    width: 80% !important;
  }

  input::placeholder{
    font-size: 15px;
  }
}

@media (max-width: 371px) {
  ul{
    justify-content: center;
  }

  ul li{
    width: 250px !important;
  }
}